.notes-container {
  /* columns: 5 20px;
    column-gap: 0rem;
    margin: 70px; */
  display: flex;
  flex-flow: row;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 24px;
  justify-content: center;
}

.note-wrapper {
  width: 200px;
  max-height: 650px;
  border-radius: 5px;
  padding-top: 0px;
  margin-top: 0;
  background-color: var(--section-color);
  padding: 16px;
  flex-direction: column;
  display: flex;
  position: relative;
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.302),
    0 1px 3px 1px rgba(60, 64, 67, 0.149);
  transition: box-shadow 0.5s;
}

.note-title {
  margin: 5px 0px 20px 0px;
  font-size: larger;
  word-break: break-all;
}

.bottom-tag {
  white-space: pre-line;
  margin-top: 20px;
  margin-bottom: 0px;
}

.note-text {
  white-space: pre-wrap;
  margin: 3px 0px 10px 0px;
  word-wrap: break-word;
}

#checklist-div {
  text-align: left;
}

.checkbox {
  position: relative;
  top: 2px;
}

.list-item-text {
  word-break: break-word;
  white-space: pre-wrap;
  text-align: initial;
  width: 148px;
}

.checked {
  text-decoration: line-through;
  font-style: italic;
  color: dimgray;
}

input[type="checkbox"] {
  background-color: ghostwhite;
  appearance: none;
  border: 1px solid #b8b3b2;
  width: 20px;
  height: 20px;
  outline: none;
  border-radius: 3px;
  cursor: pointer;
}

input[type="checkbox"]:checked {
  background-color: thistle;
  appearance: none;
  border: 1px solid #b8b3b2;
  width: 20px;
  height: 20px;
  outline: none;
  cursor: pointer;
}

input.change-input {
  width: 156px;
  word-break: break-word;
  white-space: pre-wrap;
  word-wrap: break-word;
  overflow-wrap: break-word;
  text-overflow: clip;
}

#checklist-input {
  width: -webkit-fill-available;
}

#add-button {
  padding: 10px 20px 10px 20px;
  margin: auto;
  appearance: none;
  background-color: inherit;
  border: none;
  font-size: 18px;
  border-radius: 5px;
  outline: none;
  font-family: inherit;
}

#add-button:hover {
  background-color: antiquewhite;
  border-radius: 3px;
  cursor: pointer;
  /* padding: 11px; */
}

.change-input {
  background-color: transparent;
  border: none;
  outline: none;
  font-size: inherit;
  margin: inherit;
  padding: 3px;
  width: 188px;
}

.title-edit {
  max-width: -webkit-fill-available;
  font-size: larger;
  margin-block-start: 0.83em;
  margin-block-end: 0.83em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
  padding: 0;
  border: none;
  margin: 5px 0px 20px 0px;
}

.project {
  /* background: #e7c6ed; */
  padding: 6px;
  border-radius: 10px;
  text-align: center;
  flex-shrink: 0;
  width: 77px;
  font-size: small;
  word-break: break-all;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
  border: none;
}

.note-bottom {
  display: flex;
  gap: 10px;
  align-self: baseline;
  align-content: center;
  align-self: baseline;
}

.list-item {
  display: flex;
  position: relative;
  flex-shrink: 0;
  width: 200px;
}

.delete-checklist-button {
  position: absolute;
  left: 163px;
  border: none;
  border-radius: 14px;
  outline: none;
  width: 25px;
  height: 25px;
  background-color: white;
  font-family: inherit;
  font-size: inherit;
  margin: 0;
  padding: 0;
}

.delete-checklist-button:hover {
  cursor: pointer;
  background-color: antiquewhite;
  display: block;
}

.delete-button {
  border: none;
  border-radius: 18px;
  outline: none;
  width: 25px;
  height: 25px;
  background-color: white;
  font-family: inherit;
  font-size: inherit;
  margin: 0;
  padding: 0;
  position: absolute;
  top: -8px;
  right: -6px;
}

.delete-button:hover {
  cursor: pointer;
  background-color: antiquewhite;
}
