.form-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.form {
  display: flex;
  flex-direction: column;
  width: 500px;
  position: relative;
  border-radius: 5px;
  animation: fadeInFromNone 1.7s ease-in;
  background-color: var(--section-color);
  padding: 10px;
  margin-top: 20px;
  box-shadow: 0px 0px 7px 0px rgba(60, 64, 67, 0.302),
    0 1px 1px 1px rgba(60, 64, 67, 0.149);
  height: fit-content;
}

.title-input {
  font-size: larger;
  font-weight: 600;
}

.input-field {
  outline: none;
  border: none;
  padding: 8px;
  font-size: 16px;
  border-radius: 5px;
  width: -webkit-fill-available;
  background-color: initial;
}

.temporary-form-list-item {
  margin-left: 20px;
  text-align: initial;
}

#note-input {
  margin: 0px;
  resize: none;
  padding: 15px 15px 5px 10px;
  width: -webkit-fill-available;
  height: -webkit-fill-available;
  font-family: inherit;
  font-weight: inherit;
  white-space: pre-line;
}

.note-input {
  resize: none;
  width: -webkit-fill-available;
  min-height: 100px;
  font-family: inherit;
  font-weight: inherit;
  white-space: pre-line;
  overflow: auto;
}

li {
  list-style: none;
  padding: 3px;
  text-align: center;
}

#form-bottom-input {
  display: flex;
  justify-content: center;
  background-color: initial;
  margin-top: 5px;
}

#project-input {
  text-align: center;
}
